* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: black;
}

.app {
    background-image: url('./assets/cold-bg.JPG');
    background-size: cover;
    background-position: bottom;
    
    transition: 0.4 ease-out;
    width: 400px;
    min-height: 500px;
    margin: 40px auto;
    border-radius: 10px;
} 

.app.warm{
  background-image: url('./assets/warm-bg.JPG');

}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: azure;
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: darkgray;
 
  color: darkcyan;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus{
    background-color: darkviolet;
}

.location-box .location {
color: white;
font-size: 35px;
font-weight: 500;
text-align: center;
text-shadow: rgba(0, 0, 0, 0.2);
}

.location-box .date {
 color: white;
 font-size: 20px;
 font-weight: 300;
 font-style: italic;
 text-align: center;
}

.weather-box {
  text-align: center;
  }

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;

  color: white;
  font-size: 100px;
  font-weight: 900;
  text-shadow: whitesmoke;
  text-align: center;
  box-shadow: slategrey;
}
.weather-box .weather {
 color: white;
 font-size: 48px;
 font-weight: 700;
 text-shadow: 3px 3px rgba(50, 50, 50, 0.5);

}